html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:after,
*:before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Calibri", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: #1d1d1d;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}
img.fondo {
  pointer-events: none;
  margin-bottom: 1rem;
}

section.section {
  width: 100%;
}

section.section:last-of-type {
  margin-bottom: 4rem;
}

.bold {
  font-weight: bold;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
.contenedor .contenedor-caja {
  background-color: #1d1d1d;
  box-shadow: 0 0 0.5rem 0.5rem rgba(0, 0, 0, 0.2);
  padding: 1.5rem;
  align-content: center;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .contenedor .contenedor-caja {
    width: 85%;
  }
}
.contenedor .contenedor-caja p {
  color: #ffffff;
}
.contenedor .contenedor-lista {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-content: flex-start;
}
@media (min-width: 768px) {
  .contenedor .contenedor-lista {
    flex-direction: row;
  }
}
.contenedor .contenedor-lista p {
  margin: 0;
}

.onda {
  margin: -0.4rem auto;
  padding: 0;
  width: 100%;
  pointer-events: none;
  background-color: inherit; /* Heredar el color de fondo del body */
  filter: inherit !important;
}
.onda.onda-footer {
  margin-bottom: -4.1rem;
}

.cuerpo {
  margin-top: 1.5rem;
  margin-bottom: 0;
  text-align: justify;
}
@media (min-width: 768px) {
  .cuerpo {
    margin: 10%;
    margin-top: 1.5rem;
  }
}

.lista {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: space-around;
  align-items: center;
}

.lista-comun p {
  margin: 0;
}

.listas {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  justify-content: space-around;
  align-items: center;
  padding: 3rem;
}
@media (min-width: 480px) {
  .listas {
    flex-direction: row;
  }
}

.list {
  box-shadow: 0 0 15px 3px rgb(0, 0, 0);
  border-radius: 20px;
  padding: 5px 5px;
  display: inline-flex;
  width: 80%;
  min-height: 6rem;
  max-height: 26rem;
  text-align: center;
  justify-content: center;
  align-items: center;
}
@media (min-width: 992px) {
  .list p {
    font-size: 2.5rem;
  }
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.header .superior {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 480px) {
  .header .superior {
    flex-direction: row;
  }
}
.header .superior div {
  position: relative; /* Asegúrate de que el padre tenga una posición relativa */
  width: 100%;
  height: 6rem;
  margin-top: 1rem;
}
@media (min-width: 480px) {
  .header .superior div {
    width: 19rem;
    margin-top: 0rem;
  }
}
.header .superior div:first-of-type {
  width: auto;
  max-height: 5rem;
}
@media (min-width: 480px) {
  .header .superior div:first-of-type {
    margin-left: 1rem;
  }
}
.header .superior div .logo img.logo-imagen {
  max-width: 19rem;
  max-height: 5rem;
}
.header .superior div .boton-donar {
  text-align: center;
  color: #ffffff;
  background-color: #bf1616;
  text-decoration: none;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-weight: bold;
}
.header .superior div .boton-donar:hover {
  background-color: #1d1d1d;
}
.header .button-wrapper {
  position: relative;
  animation: fadeIn 0.5s ease-in-out;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: 4rem;
  background-color: #bf1616;
}
@media (min-width: 768px) {
  .header .button-wrapper {
    display: none;
  }
}
.header .button-wrapper .barra {
  position: absolute;
  width: 2rem;
  right: 1rem;
  cursor: pointer;
}

nav {
  display: none;
  width: 100%;
  z-index: 2;
}
@media (min-width: 768px) {
  nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
}
nav.open {
  display: block;
}
@media (min-width: 768px) {
  nav.open {
    display: flex;
  }
}
nav.menu {
  animation: fadeIn 0.5s ease-in-out;
  background-color: #bf1616;
}
@media (min-width: 768px) {
  nav.menu {
    width: 100%;
  }
}
nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  background-color: #bf1616;
}
nav ul ul {
  display: none;
  position: relative;
  width: 100%;
  background-color: #b61515;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  animation: fadeIn 0.5s ease-in-out;
}
@media (min-width: 768px) {
  nav ul ul {
    position: absolute;
    top: 100%;
    left: 0;
    width: 20rem;
  }
}
nav ul ul ul {
  position: relative;
  left: 0;
  top: 100%;
  background-color: #ad1414;
}
@media (min-width: 768px) {
  nav ul ul ul {
    position: absolute;
    left: 100%;
    top: 0;
  }
}
nav ul li {
  position: relative;
  display: block;
}
@media (min-width: 768px) {
  nav ul li {
    display: inline-block;
  }
  nav ul li:hover > ul {
    display: block;
  }
}
nav ul li li {
  display: block;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}
nav ul li a {
  display: block;
  padding: 2rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  color: #ffffff;
}
nav ul li:hover {
  background-color: #1d1d1d;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.carrucel {
  height: auto;
  max-height: 60rem;
  overflow: hidden;
  position: relative;
}
.carrucel .overlay {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  position: absolute;
  width: 100%;
}
.carrucel .overlay .contenido-slider {
  z-index: 1;
}
.carrucel .overlay .contenido-slider h2 {
  text-shadow: 0.3rem 0.1rem 0.5rem rgba(0, 0, 0, 0.5);
  color: #ffffff;
  margin: 0;
}
@media (min-width: 480px) {
  .carrucel .overlay .contenido-slider h2 {
    font-size: 6rem;
  }
}
.carrucel .slider-frame {
  display: block;
  width: 100%;
  height: 40%;
  overflow: hidden;
  z-index: 0;
}
.carrucel .slider-frame ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  padding: 0;
  width: 400%;
  height: auto;
  animation: slide 20s infinite alternate ease-in-out;
}
.carrucel .slider-frame li {
  width: 100%;
  list-style: none;
}
.carrucel .slider-frame img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

@keyframes slide {
  0% {
    margin-left: 0;
  }
  20% {
    margin-left: 0;
  }
  25% {
    margin-left: -100%;
  }
  45% {
    margin-left: -100%;
  }
  50% {
    margin-left: -200%;
  }
  70% {
    margin-left: -200%;
  }
  75% {
    margin-left: -300%;
  }
  100% {
    margin-left: -300%;
  }
}
.contenedor-informacion {
  text-align: center;
  margin: 2rem 0;
}
.contenedor-informacion h1 {
  color: #bf1616;
}
@media (min-width: 768px) {
  .contenedor-informacion h1 {
    font-size: 8rem;
  }
}
.contenedor-informacion p {
  text-transform: uppercase;
  margin: 0;
  font-size: 2.2rem;
}

.contenedor-programas {
  background-color: #bf1616;
}

.programas {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  padding: 2rem 0;
}
.programas .programa {
  box-shadow: 0 0 1rem 0.2rem rgba(0, 0, 0, 0.15);
  padding: 0.2rem;
  border-radius: 2rem;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 300ms ease-in;
  width: 15rem;
  height: 11rem;
  margin: 0 2.5rem;
}
@media (min-width: 480px) {
  .programas .programa {
    width: 19rem;
    height: 15rem;
  }
}
.programas .programa:hover {
  background-color: #1d1d1d;
  transform: scale(1.2);
}
.programas .programa .programa-imagen {
  width: 7rem;
  height: 7rem;
}
.programas .programa .programa-detalles h1 {
  text-align: center;
  color: #ffffff;
  font-size: 2.4rem;
}
@media (min-width: 480px) {
  .programas .programa .programa-detalles h1 {
    font-size: 3rem;
  }
}

.contenedor-eventos {
  background-color: #1d1d1d;
  padding-bottom: 2rem;
}
.contenedor-eventos .eventos {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.contenedor-eventos .evento {
  display: block;
  border: 0.1rem solid #1d1d1d;
  background-color: #ffffff;
  border-radius: 1.5rem;
  box-shadow: 0 0 1rem 0.5rem rgba(0, 0, 0, 0.15);
  margin: 1rem;
}
@media (min-width: 992px) {
  .contenedor-eventos .evento {
    max-width: 135rem;
    max-height: 42rem;
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 1rem;
  }
  .contenedor-eventos .evento img.evento-imagen {
    max-width: 42rem;
    justify-self: center;
    align-self: center;
    margin-left: 1rem;
  }
}
.contenedor-eventos .evento img.evento-imagen {
  border-radius: 0.9rem;
}
.contenedor-eventos .evento .evento-contenido {
  width: 95%;
  margin: 0 auto;
}
.contenedor-eventos .evento .evento-contenido h2 {
  margin: 1rem 0;
}
.contenedor-eventos .evento .evento-contenido p {
  text-align: justify;
  font-size: 1.6rem;
  margin: 1rem 0;
}
@media (min-width: 240px) {
  .contenedor-eventos .evento .evento-contenido p {
    font-size: 2rem;
  }
}
.contenedor-eventos .evento .evento-contenido p.evento-fecha {
  text-align: right;
  font-weight: bold;
}

.contenedor-testimonios h1 {
  color: #1d1d1d;
  margin: 2rem 0;
}
.contenedor-testimonios .testimonios {
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
  margin: 0 1rem;
  animation: fadeIn 0.5s ease-in-out;
}
.contenedor-testimonios .testimonios.active {
