  background-color: inherit;
  width: 50%;
}
main.about .section .historia-content::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 25px;
  right: -13px;
  background-color: #ffffff;
  border: 4px solid #1d1d1d;
  top: 20px;
  border-radius: 50%;
  z-index: 1;
}
main.about .section .left {
  left: 0;
}
main.about .section .right {
  left: 50%;
}
main.about .section .left::before {
  /*Flecha en computadora*/
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid #1d1d1d;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent #1d1d1d;
}
main.about .section .right::before {
  /* Flecha en computadora*/
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid #1d1d1d;
  border-width: 10px 10px 10px 0;
  border-color: transparent #1d1d1d transparent transparent;
}
main.about .section .right::after {
  left: -13px;
}
