    width: 100%;
    padding-left: 70px;
    padding-right: 25px;
  }
  main.about .section .historia-content::before {
    /*Flecha en telefonos*/
    left: 60px;
    border: medium solid #1d1d1d;
    border-width: 10px 10px 10px 0;
    border-color: transparent #1d1d1d transparent transparent;
  }
  main.about .section .left::after,
